/**
 * Breakpoints
 */

.wdg__rating {
  position: fixed;
  z-index: 998;
  bottom: 60px;
  width: 300px;
  height: 60px;
  background: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid #8b8b8b;
  box-shadow: 0px 10px 50px -17px rgba(0, 0, 0, 0.58);
}

.wdg__rating.bottom_center {
  left: 50%;
  transform: translateX(-50%);
}

.wdg__rating.bottom_right {
  right: 40px;
}

.wdg__rating.bottom_left {
  left: 40px;
}

@media (max-width: 767px) {
  .wdg__list-item .link {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    display: block;
  }
}

@media (max-width: 767px) {
  .wdg__list-item--cell {
    display: flex;
    align-items: center;
  }
}

@media (max-width: 767px) {
  .wdg__list-item--cell .rating {
    margin: 0 0 0 12px;
  }
}